.question
{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.singleQuestion
{
    width: 95%;
    min-height: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border: 5px solid #e4c20cf7;
    padding: 20px;
    margin-top: 10px;
}
.options
{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex: 1;
    align-items: center;
    justify-content: space-around;
    margin: 10px;
}
h1
{
    color: white;
    font-family: "Poppins",sans-serif;
}
h2
{
    color: #ffca00;
    font-family: "Poppins",sans-serif;
}
.singleOption
{
    width: 46%;
    height: 50px;
    padding: 15px 20px;
    margin: 10px;
    box-shadow: 0 0 2px black;
}
@media (max-width:800px)
{
    .singleOption{
        width: 100%;
    }
    .controls
    {
        width: 100%;
    }
}
.select
{
    background-color: rgb(7, 207,0);
    color: white;
    box-shadow: 0 0 1px black;
}
.wrong
{
    background-color: rgb(233,0,0);
    color: white;
    box-shadow: 0 0 1px black;
}
.controls 
{
    display: flex;
    width: 100%;
    justify-content: space-around;
}